.App {
  text-align: center;
  display: flex;
  align-items: center;
  flex-direction: column;
  padding: 0.5%;
}

.action-buttons {
  display: flex;
  justify-content: flex-end;
  padding-right: 1.5%;
  padding-top: 0.2%;
}

.save-button {
  background-color: rgb(2, 117, 216);
  border: rgb(2, 117, 216);
  color: white;
  padding: 7px 12px;
  margin-right: 0.5%;
  cursor: pointer;
}

.clear-button {
  border: 1px solid rgb(204, 204, 204);
  background: white;
  color: #333333;
  padding: 7px 12px;
  cursor: pointer;
}

.sigCanvas {
  border: 1px solid rgb(204, 204, 204);
}
